import React from 'react'
import {HowtoContainer, HowtoWrapper, HowtoCard, HowtoH1, HowtoIcon, HowtoH2, HowtoP} from './HowtoElements';
import Icon1 from '../../images/warehouse.svg';
import Icon2 from '../../images/truck.svg';
import Icon3 from '../../images/location.svg';
const HowTo = () => {
    return (
        <>
          <HowtoContainer id="howto">
              <HowtoH1>
                  How To Use
              </HowtoH1>
              <HowtoWrapper>
                  <HowtoCard>
                      <HowtoIcon src= {Icon1}/>
                      <HowtoH2>
                          Step 1
                      </HowtoH2>
                      <HowtoP>
                          We help you to get access to a premium logistic services
                      </HowtoP>
                  </HowtoCard>
                  <HowtoCard>
                      <HowtoIcon src={Icon2} />
                      <HowtoH2>
                          Step 2
                      </HowtoH2>
                      <HowtoP>
                          We help you to get access to a premium logistic services
                      </HowtoP>
                  </HowtoCard>
                  <HowtoCard>
                      <HowtoIcon src={Icon3} />
                      <HowtoH2>
                          It's Done
                      </HowtoH2>
                      <HowtoP>
                          We help you to get access to a premium logistic services
                      </HowtoP>
                  </HowtoCard>
              </HowtoWrapper>
              </HowtoContainer>  
        </>
    )
}

export default HowTo
