import React from "react";
import {animateScroll as scroll} from 'react-scroll'
import { FaFacebook, FaInstagram, FaLinkedin } from "react-icons/fa";
import {
  FooterContainer,
  FooterWrap,
  FooterLinksContainer,
  FooterLinksWrapper,
  FooterLinkItems,
  FooterLinkTitle,
  FooterLink,
  SocialMedia,
  SocialMediaWrap,
  SocialLogo,
  WebsiteRights,
  SocialIcons,
  SocialIconLinks,
} from "./FooterElements";

const Footer = () => {
  const toggleHome = () => {
    scroll.scrollToTop();
  };

  return (
    <FooterContainer>
      <FooterWrap>
        <FooterLinksContainer>
          <FooterLinksWrapper>
            <FooterLinkItems>
              <FooterLinkTitle> About Us</FooterLinkTitle>
              <FooterLink to="/signin">About Us</FooterLink>
              <FooterLink to="/signin">Contact Us</FooterLink>
              <FooterLink to="/signin">Privacy Policy</FooterLink>
              <FooterLink to="/signin">Terms and COndition</FooterLink>
              <FooterLink to="/signin">Facebook</FooterLink>
              <FooterLink to="/signin">Twitter</FooterLink>
              <FooterLink to="/signin">Linkedin</FooterLink>
            </FooterLinkItems>

            <FooterLinkItems>
              <FooterLinkTitle> About Us</FooterLinkTitle>
              <FooterLink to="/signin">About Us</FooterLink>
              <FooterLink to="/signin">Contact Us</FooterLink>
              <FooterLink to="/signin">Privacy Policy</FooterLink>
              <FooterLink to="/signin">Terms and COndition</FooterLink>
              <FooterLink to="/signin">Facebook</FooterLink>
              <FooterLink to="/signin">Twitter</FooterLink>
              <FooterLink to="/signin">Linkedin</FooterLink>
            </FooterLinkItems>
          </FooterLinksWrapper>
        </FooterLinksContainer>
        <SocialMedia>
          <SocialMediaWrap>
            <SocialLogo to="/" onClick={toggleHome}>BIDBAZZAR</SocialLogo>
            <WebsiteRights>
              BIDBAZAAR copyright {new Date().getFullYear()}, All Rights
              Reserved
            </WebsiteRights>
            <SocialIcons>
              <SocialIconLinks
                href="//www.facebook.com"
                target="_blank"
                aria-label="Facebook"
              >
                <FaFacebook />
              </SocialIconLinks>
              <SocialIconLinks href="/" target="_blank" aria-label="Twitter">
                <FaInstagram />
              </SocialIconLinks>
              <SocialIconLinks href="/" target="_blank" aria-label="LinkedIN">
                <FaLinkedin />
              </SocialIconLinks>
            </SocialIcons>
          </SocialMediaWrap>
        </SocialMedia>
      </FooterWrap>
    </FooterContainer>
  );
};

export default Footer;
